	module.exports = {"pathContext":{"docHtml":"<h2>Strings &#x26; Bytes</h2>\n<p>Miscellaneous string &#x26; byte encoding and decoding functionality provided for compatibility across supported versions of V8 and Node. Implemented by NAN to ensure that all encoding types are supported, even for older versions of Node where they are missing.</p>\n<ul>\n<li><a href=\"#api_nan_encoding\"><b><code>Nan::Encoding</code></b></a></li>\n<li><a href=\"#api_nan_encode\"><b><code>Nan::Encode()</code></b></a></li>\n<li><a href=\"#api_nan_decode_bytes\"><b><code>Nan::DecodeBytes()</code></b></a></li>\n<li><a href=\"#api_nan_decode_write\"><b><code>Nan::DecodeWrite()</code></b></a></li>\n</ul>\n<p><a name=\"api_nan_encoding\"></a></p>\n<h3>Nan::Encoding</h3>\n<p>An enum representing the supported encoding types. A copy of <code>node::encoding</code> that is consistent across versions of Node.</p>\n<p>Definition:</p>\n<pre><code class=\"language-c++\">enum Nan::Encoding { ASCII, UTF8, BASE64, UCS2, BINARY, HEX, BUFFER }\n</code></pre>\n<p><a name=\"api_nan_encode\"></a></p>\n<h3>Nan::Encode()</h3>\n<p>A wrapper around <code>node::Encode()</code> that provides a consistent implementation across supported versions of Node.</p>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">v8::Local&#x3C;v8::Value> Nan::Encode(const void *buf,\n                                 size_t len,\n                                 enum Nan::Encoding encoding = BINARY);\n</code></pre>\n<p><a name=\"api_nan_decode_bytes\"></a></p>\n<h3>Nan::DecodeBytes()</h3>\n<p>A wrapper around <code>node::DecodeBytes()</code> that provides a consistent implementation across supported versions of Node.</p>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">ssize_t Nan::DecodeBytes(v8::Local&#x3C;v8::Value> val,\n                         enum Nan::Encoding encoding = BINARY);\n</code></pre>\n<p><a name=\"api_nan_decode_write\"></a></p>\n<h3>Nan::DecodeWrite()</h3>\n<p>A wrapper around <code>node::DecodeWrite()</code> that provides a consistent implementation across supported versions of Node.</p>\n<p>Signature:</p>\n<pre><code class=\"language-c++\">ssize_t Nan::DecodeWrite(char *buf,\n                         size_t len,\n                         v8::Local&#x3C;v8::Value> val,\n                         enum Nan::Encoding encoding = BINARY);\n</code></pre>","docPath":"argo-ci/node_modules/nan/doc/string_bytes","proj":"argo-ci"}}

